.Snackbar {
    padding: .7em;
    margin: 1em;
    background-color: var(--gunmetal);
    color: #fefefe;
    box-shadow: 0 0 2px 2px rgba(0,0,0,0.1);
    opacity: 0;
    height: 0;
    width: 0;
    appearance: none;
    transition: opacity 200ms;
    border-radius: .3em;
    max-width: 600px;
}
.Snackbar.is-visible {
    opacity: 1;
    height: auto;
    width: auto;
}
.Snackbar-Surface {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.Snackbar-Label {
    font-size: .85em;
    letter-spacing: .1em;
}
.Snackbar-Action-Button {
    border: 0;
    background-color: transparent;
    padding: .3em;
    border-radius: .2em;
}
.Snackbar-Action-Button:hover {
    background-color: rgba(5, 142, 217, 0.1);
    transition: background-color 200ms;
}
.Snackbar-Button-Label {
    color: var(--info);
    background-color: transparent;
    font-size: 1.05em;
    font-weight: 600;
    text-transform: uppercase;
}